import React, { Component } from 'react';
import Slider from "react-slick";
import './slice.css';

const Slice = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows:false,
  focusOnSelect:true
  };
    return (
<div className="imagen">
      <Slider {...settings} className="sliceRoot">
        <div>
        <img src={require('../../resources/images/project/monuks/01.png')} className='imagenPrincipal'/>
        </div>
        <div>
          <img src={require('../../resources/images/project/monuks/02.png')} className='imagenPrincipal'/>
        </div>
        <div>
          <img src={require('../../resources/images/project/monuks/03.png')} className='imagenPrincipal'/>
        </div>
        <div>
          <img src={require('../../resources/images/project/monuks/04.png')} className='imagenPrincipal'/>
        </div>
        <div>
          <img src={require('../../resources/images/project/monuks/05.png')} className='imagenPrincipal'/>
        </div>
        <div>
          <img src={require('../../resources/images/project/monuks/06.png')} className='imagenPrincipal'/>
        </div>
        <div>
          <img src={require('../../resources/images/project/monuks/07.png')} className='imagenPrincipal'/>
        </div>
        <div>
          <img src={require('../../resources/images/project/monuks/08.png')} className='imagenPrincipal'/>
        </div>
      </Slider>
    </div>
    )
}
export default Slice



